import { useTranslation } from "react-i18next";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import StudentAttendanceClassCard from "./StudentAttendanceClassCard";
import { useInView } from "react-intersection-observer";
import { useCallback, useEffect, useState } from "react";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { useStudentStatsClassList } from "../../../Hooks/ApiHooks/StudentManager/student-manager-api";

const StudentAttendanceClass = ({ instituteId }) => {
  const { t } = useTranslation();

  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const { studentStatsClassList, studentStatsClassListLoading } =
    useStudentStatsClassList({
      data: {
        id: instituteId,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !instituteId,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (studentStatsClassList?.list?.length) {
      let obj = nextPage(studentStatsClassList?.list?.length);
      setState(() => obj);
    }
  }, [studentStatsClassList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) {
      if (!studentStatsClassListLoading) onDebounce(e.target.value);
    } else {
      setPage(() => 1);
      setSearch("");
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("classes_list")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {studentStatsClassList?.list?.map((cls, index) =>
            studentStatsClassList?.list?.length === index + 1 ? (
              <div key={cls?._id} ref={ref}>
                <StudentAttendanceClassCard cls={cls} />
              </div>
            ) : (
              <StudentAttendanceClassCard cls={cls} key={cls?._id} />
            )
          )}
          {studentStatsClassListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            studentStatsClassList?.list?.length === 0 && (
              <EmptyMessage title="No classes found related this search." />
            )
          ) : (
            studentStatsClassList?.list?.length === 0 && (
              <EmptyMessage title="No classes." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default StudentAttendanceClass;
