import { useNavigate, useParams } from "react-router-dom";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import style from "../InstituteAnalytic.module.css";

const StudentAttendanceClassCard = ({ cls }) => {
  const params = useParams();
  const navigate = useNavigate();
  const onNavigate = () => {
    navigate(`/q/${params.username}/institute/member/analytic?type=student`, {
      state: {
        clsId: cls?._id,
        clsName: `${cls?.className ?? ""} - ${cls?.classTitle ?? ""}`,
      },
    });
  };
  return (
    <>
      <div
        className={style.staclc_card}
        style={{
          justifyContent: "space-between",
          cursor: "pointer",
        }}
        onClick={onNavigate}
      >
        <div className={style.staclc_card_container}>
          <img
            src={
              cls?.classTeacher?.staffProfilePhoto
                ? `${imageShowUrl}/${cls?.classTeacher?.staffProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
          />
          <div className={style.staclc_card_inner}>
            <h6>
              {cls?.className ?? ""} - {cls?.classTitle ?? ""}
            </h6>
            <p>
              {`${cls?.classTeacher?.staffFirstName ?? ""} ${
                cls?.classTeacher?.staffMiddleName ?? ""
              } ${cls?.classTeacher?.staffLastName ?? ""}`}
            </p>
          </div>
        </div>
        <div className={style.stc_stats}>
          <span>{cls?.studentCount ?? 0}</span>
        </div>
      </div>
    </>
  );
};

export default StudentAttendanceClassCard;
